.Component-center {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

button {
  cursor: pointer;
  padding: 1px 20px;
  font-size: 2rem;
  border: 1px solid white;
  outline: none;
  border-radius: 25px;
  transition: all 0.35s;
}

button:hover,
button:focus {
  transition: all 0.25s;
  background-color: rgba(0, 0, 0, .7);
}

.calculator-grid>button {
  cursor: pointer;
  font-size: 2rem;
  border: 1px solid white;
  outline: none;
  box-shadow: inset 0 3px 6px 0 rgba(0, 0, 0, 0.75);
  border-radius: 0px;
  /* filter: drop-shadow(0 0 rem rgba(0, 0, 0, 0.75)); */
  /* background-color: rgb(6, 182, 212, .9); */
}

.calculator-grid>button:hover,
.calculator-grid>button:focus {
  background-color: rgba(0, 0, 0, 0.5);
}

